import { Container, Typography, Grid2, Card, CardContent } from "@mui/material";

const API_URL = "http://localhost:8080";
// Get CSRF token from the cookie
const getCSRFToken = () => {
  const token = document.cookie
    .split("; ")
    .find((row) => row.startsWith("XSRF-TOKEN="))
    ?.split("=")[1];
  return token || ""; // Return token or an empty string if not found
};

const bookRepairShop = (repairShopId) => {
  // Step 1: Get CSRF Cookie First
  fetch(API_URL + "/sanctum/csrf-cookie", {
    method: "GET",
    credentials: "include",
  })
    .then(() => {
      return fetch(API_URL + "/bookings", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "X-XSRF-TOKEN": getCSRFToken(), // Include CSRF token if using Laravel Sanctum
        },
        credentials: "include",
        body: JSON.stringify({ repair_shop_id: repairShopId }),
      });
    })
    .then((res) => {
      if (!res.ok) {
        console.log(res);
        throw new Error("Not able to book");
      }

      return res.json();
    })
    .then(() => alert("Booking successful!"))
    .catch((err) => alert(err));
};

const cancelBooking = () => {
  fetch(API_URL + "/bookings/today", {
    method: "DELETE",
    headers: {
      "Content-Type": "application/json",
      "X-XSRF-TOKEN": getCSRFToken(), // Include CSRF token if using Laravel Sanctum
    },
    credentials: "include",
  })
    .then((res) => {
      if (!res.ok) {
        throw new Error("Not able to cancel booking!");
      }

      return res.json();
    })
    .then(() => alert("Booking cancelled!"))
    .catch((err) => alert(err.response.data.message));
};

const RepairShopList = ({ shops }) => {
  return (
    <div>
      <Container>
        <Typography variant="h4" gutterBottom>
          Vélo Repair Shops
        </Typography>
        <Grid2 container spacing={3}>
          {shops.map((shop) => (
            <Grid2 item xs={12} sm={6} md={4} key={shop.id}>
              <Card>
                <CardContent>
                  <Typography variant="h6">{shop.name}</Typography>
                  <Typography variant="body2">{shop.address}</Typography>
                  <Typography variant="body2">📞 {shop.phone}</Typography>
                  <p>Available Slots: {shop.remaining_slots}</p>
                  <button
                    onClick={() => bookRepairShop(shop.id)}
                    disabled={shop.remainingSlots <= 0}
                  >
                    Book Now
                  </button>
                </CardContent>
              </Card>
            </Grid2>
          ))}
        </Grid2>
      </Container>

      <button onClick={cancelBooking}>Cancel My Existing Booking</button>
    </div>
  );
};

export default RepairShopList;
