import "./App.css";
import RepairShopList from "./RepairShop";
import Map from "./Map";
import { liteClient as algoliasearch } from "algoliasearch/lite";
import { InstantSearch, SearchBox, Hits } from "react-instantsearch";
import { Container, Typography, Grid2, Card, CardContent } from "@mui/material";
import React, { useEffect, useState } from "react";
import {
  GoogleMap,
  Marker,
  useLoadScript,
  LoadScript,
} from "@react-google-maps/api";

const searchClient = algoliasearch(
  process.env.REACT_APP_ALGOLIA_APP_ID,
  process.env.REACT_APP_ALGOLIA_SEARCH_ONLY_API_KEY
);

function App() {
  const [searching, setSearching] = useState(false);
  const [shops, setShops] = useState([]);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [user, setUser] = useState(null);

  const { isLoaded } = useLoadScript({
    googleMapsApiKey: "AIzaSyCYzRPsQtVRZAzdC33MJtplrd71Mxf0U00",
  });

  const API_URL = "http://localhost:8080";

  useEffect(() => {
    // Step 1: Get CSRF Cookie First
    fetch(API_URL + "/sanctum/csrf-cookie", {
      method: "GET",
      credentials: "include",
    })
      .then(() => {
        // Step 2: Check if the user is authenticated
        return fetch(API_URL + "/user", {
          method: "GET",
          credentials: "include",
        });
      })
      .then((res) => {
        if (!res.ok) {
          throw new Error("Not authenticated");
        }
        return res.json(); // If authenticated, parse JSON response
      })
      .then((data) => {
        console.log("Logged in User:", data[0]);
        setIsAuthenticated(true); // Access the parsed JSON
      })
      .then(() => {
        // Step 2: Check if the user is authenticated
        return fetch(API_URL + "/repairshops", {
          method: "GET",
          credentials: "include",
        });
      })
      .then((res) => {
        if (!res.ok) {
          throw new Error("Not able to retrieve shop lists");
        }

        return res.json();
      })
      .then((data) => {
        console.log(data);

        return setShops(data);
      })
      .catch(() => {
        window.location.href = API_URL + "/register"; // Redirect if not authenticated
      });
  }, []);

  function toggleSearching(e) {
    return setSearching(e.target.value !== "");
  }

  function Hit({ hit }) {
    if (hit) {
      console.log(hit);
      return (
        <Card>
          <article>
            <p>{hit.phone}</p>
            <h1>{hit.name}</h1>
            <p>{hit.address}</p>
          </article>
        </Card>
      );
    }
  }

  return (
    <div className="App">
      <InstantSearch searchClient={searchClient} indexName="repair_shops">
        <SearchBox onKeyUp={(e) => toggleSearching(e)} />

        {searching && <Hits hitComponent={Hit} />}
      </InstantSearch>

      <Map isLoaded={isLoaded} shops={shops} />
      <RepairShopList className="repair" shops={shops} />
    </div>
  );
}

export default App;
